import React from "react"
import { Link } from "gatsby"

export const Comments = ({ slug }) => {
  return (
    <div>
      <hr />
      <h2>Comments</h2>
      <h3>Add a comment</h3>
      <form
        method="POST"
        action="https://bluealloy-blog.herokuapp.com/v3/entry/github/rakita/bluealloy/master/comments"
      >
        <input name="options[redirect]" type ="hidden" value ="https://rakita.github.io/"/>
        <input name="options[slug]" type ="hidden" value ={slug}/>

        <input name="options[redirect]" type ="hidden" value ="https://my-site.com"/>
        <input name="fields[name]" type="text" placeholder="Name" required />
        <input name="fields[email]" type="email" placeholder="Email" required />
        <textarea name="fields[message]" placeholder="Comment" required />
        <button type="submit">Submit Comment</button>
      </form>
    </div>
  )
}
